html {
	font-size: 100px;
	font-size: 31.25vw;
	overflow-x: hidden;
	&.overflowY {
		overflow-y: hidden; } }

body {
	background-color: $black;
	color: $white;
	* {
		font-size: 1em;
		font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
		line-height: 1.4em; } }

picture {
	display: flex; }
a {
	display: flex;
	text-decoration: none;
	color: $white; }

.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 10;
	&.active {
		z-index: 21;
		header {
			background-color: transparent; }
		.header__phones {
			display: none; } }
	header {
		display: flex;
		height: .61em;
		align-items: center;
		background-color: $black; }
	button {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0;
		margin-left: .2em;
		border: none;
		background-color: transparent;
		transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
		outline: none;
		&.active {
			line {
				stroke: $white;
				&:first-child {
					transform: rotateZ(-45deg);
					transform-origin: 75% 0%; }
				&:nth-child(2) {
					opacity: 0; }
				&:last-child {
					transform: rotateZ(45deg);
					transform-origin: 80% 80%; } } }
		svg {
			position: relative;
			width: .25em;
			height: .13em;
			transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
			line {
				transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83); } } }
	&__phones {
		display: grid;
		gap: .09em 0;
		margin-right: .15em;
		margin-left: auto;
		a {
			font-size: .14em; } } }
.about-company {
	margin-top: 1.11em;
	&__h2 {
		margin-bottom: .2em;
		h2 {
			text-align: center;
			font-size: .36em;
			font-weight: bold; } }
	&__video {
		margin-bottom: .2em;
		display: flex;
		align-items: center;
		justify-content: center;
		video {
			width: 3.2em;
			height: 2.77em; } }
	&-description {
		display: grid;
		gap: .2em 0;
		padding: 0 .15em;
		p {
			font-size: .16em;
			a {
				display: contents;
				text-decoration: underline;
				color: #FA903C; } }
		ul {
			list-style: none;
			margin-left: .1em;
			li {
				list-style: '- ';
				font-size: .16em; } } } }

.modalW {
	position: fixed;
	z-index: 20;
	top: 0;
	right: 100%;
	width: 100%;
	height: 100%;
	background-color: $black;
	background: url(/img/modal-bg.jpg) no-repeat;
	background-size: cover;
	background-position: 0 0;
	transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
	&.active {
		right: 0; }
	&__h2 {
		margin-top: .24em;
		margin-bottom: .22em;
		h2 {
			font-size: .18em;
			font-weight: bold;
			text-align: center; } }
	nav {
		position: relative;
		padding: 0 .2em;
		section {
			border: .01em solid $link;
			width: max-content;
			padding: .15em .2em;
			margin: .16em auto 0;
			ul {
				list-style: none;
				display: grid;
				gap: .1em 0;
				li {

					a {
						display: flex;
						flex-flow: column;
						font-size: .14em; } } } }
		.row {
			margin-bottom: .2em;
			ul {
				list-style: none;
				display: flex;
				li {
					&:not(:first-child) {
						margin-left: auto; }
					a {
						font-size: .14em;
						font-weight: bold; } } } }
		.row2 {
			ul {
				list-style: none;
				display: flex;
				li {
					&:first-child {
						margin-right: .5em; }
					&:last-child {
						display: flex;
						align-items: center;
						a {
							color: $link; }
						svg {
							margin-top: 10%;
							margin-left: .02em;
							width: .07em;
							height: .07em;
							path {
								fill: $link; } } }
					a {
						font-size: .14em;
						font-weight: bold; } } } } }
	&-bottom {
		position: absolute;
		bottom: 0;
		padding: 0 .15em .24em;
		p, a {
			font-size: 0.14em; }
		button {
			display: flex;
			align-items: center;
			justify-content: center;
			width: 1.44em;
			height: .26em;
			border: none;
			border-bottom: .01em solid $link;
			outline: none;
			background: transparent;
			padding: 0;
			margin-bottom: .2em;
			p {
				color: $white;
				font-size: .18em;
				font-weight: bold; } }
		.row {
			display: grid;
			grid-auto-flow: column;
			gap: 0 .27em;
			.col {
				display: grid;
				&:first-child {
					gap: .05em 0;
					width: max-content; }
				&:nth-child(2) {
					gap: 0 .08em;
					width: max-content;
					grid-auto-flow: column; }
				a {
					display: flex;
					height: max-content; } } }
		.row2 {
			margin-top: .2em;
			display: grid;
			gap: .05em 0; } } }

.vacancy {
	margin-top: .5em;
	// padding: 0 .15em
	&__h2 {
		padding: 0 .15em;
		margin-bottom: .3em;
		h2 {
			text-align: center;
			font-weight: bold;
			font-size: .24em; } }
	.pre {
		padding: 0 .9375em;
		font-size: .16em;
		text-align: center; }
	.swiper {
		&-container {
			position: relative;
			margin-top: .2em; }
		&-slide {
			position: relative;
			padding: 0 .15em;
			img {
				width: 2.71em;
				height: 2.48em; }
			p {
				text-align: center;
				font-size: .18em; } }
		&-pagination {
			margin-top: .25em;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			&-bullet {
				background: #C4C4C4;
				&-active {
					background: linear-gradient(180deg, #FFC300 0%, #FF7304 51.43%); } } } } }

.choose {
	margin-top: .4em;
	margin-bottom: .5em;
	padding: 0 .15em;
	&__h2 {
		h2 {
			text-align: center;
			font-size: .24em;
			font-weight: bold; } }
	&-container {
		margin-top: .63em;
		display: grid;
		gap: .3em 0; }
	&__card {
		display: flex;
		flex-flow: column;
		align-items: center;
		justify-content: center;
		img {
			width: 1.04em;
			height: 1.04em; }
		p {
			display: flex;
			flex-flow: column;
			font-size: .16em;
			text-align: center; } } }

.connect-to-team {
	border-top: .01em solid $white;
	border-bottom: .01em solid $white;
	padding: 0 .15em;
	&__h2 {
		margin-top: .3em;
		h2 {
			display: flex;
			flex-flow: column;
			font-size: .18em;
			font-weight: bold;
			text-align: center; } }
	.forma {
		margin-top: .44em;
		margin-bottom: .3em;
		display: grid;
		gap: .3em 0;
		section {
			width: 2em;
			display: grid;
			gap: .25em 0;
			margin: 0 auto;
			input {
				padding: 0;
				padding-left: 1em;
				border: none;
				height: 2.1429em;
				font-size: .14em;
				&::placeholder {
					color: $black; }
				&.invalid_phone {
					border: .01em solid red; } } }
		button {
			padding: 0;
			border: none;
			width: 2em;
			height: .35em;
			display: flex;
			align-items: center;
			justify-content: center;
			margin: 0 auto;
			background: linear-gradient(180deg, #FFC300 0%, #FF7304 51.43%);
			p {
				color: $white;
				font-size: .14em;
				font-weight: bold; } }
		p {
			display: flex;
			flex-flow: column;
			align-items: center;
			font-size: .12em;
			a {
				border-bottom: .01em solid $white; } } } }

.contacts {
	margin-top: .5em;
	margin-bottom: .5em;
	padding: 0 .15em;
	&__h2 {
		margin-bottom: .3em;
		h2 {
			text-align: center;
			font-size: .24em;
			font-weight: bold; } }
	&-column {
		display: grid;
		gap: .2em 0;
		.row {
			display: grid;
			gap: .1em 0; }
		p {
			font-size: .18em;
			font-weight: bold; }
		a {
			font-size: .16em; }
		span {
			display: flex;
			font-size: .16em; }
		.col {
			display: grid;
			gap: .1em 0;
			&__links {
				display: grid;
				gap: .1em 0; } } } }

.map {
	width: 3.21em;
	height: 3.21em;
	.ymaps-2-1-77-copyrights-pane {
		display: none; } }

footer {
	position: relative;
	padding: .5em .25em .4em;
	.column {
		position: relative;
		display: grid;
		gap: 0 .25em;
		grid-auto-flow: column;
		.nav {
			ul {
				list-style: none;
				display: grid;
				gap: .05em 0;
				li {
					display: flex;
					a {
						display: flex;
						font-size: .14em; } } } }
		.uslugi {
			ul {
				list-style: none;
				display: grid;
				gap: .05em 0;
				li {
					display: flex;
					a {
						display: flex;
						font-size: .14em; } } } } }
	.soc {
		margin-top: .35em;
		display: grid;
		gap: .18em 0;
		&-p {
			p {
				font-size: .14em; } }
		&-icons {
			display: grid;
			grid-auto-flow: column;
			width: max-content;
			gap: 0 .08em; } }
	#footer-img {
		pointer-events: none;
		z-index: -1;
		position: absolute;
		transform: scale(-1, 1);
		opacity: .4;
		top: 0;
		right: 0;
		width: auto;
		height: 100%; } }
