@media screen and ( min-width: 568px ) and ( orientation: landscape ) {
    html {
        font-size: 17.6056565vw; }
    .modalW {
        nav {
            display: flex;
            justify-content: center;
            section {
                position: absolute;
                right: 0.1em;
                top: .1em; }
            .row {
                ul {
                    li {
                        margin-right: .15em; } } }
            .row2 {
                ul {
                    li {
                        &:first-child {
                            margin-right: .15em; } } } } } }
    .vacancy {
        .swiper-slide {
            text-align: center; } }
    .choose-container {
        grid-auto-flow: column;
        gap: 0 .2em;
        margin-top: .2em; }
    .connect-to-team {
        .forma {
            section {
                grid-auto-flow: column;
                gap: 0 .25em;
                width: 4.35em; } } }
    .contacts-column {
        grid-auto-flow: column;
        .row {
            &:last-child {
                gap: .4em 0; } } }
    .map {
        width: 100%; } }
